.header-hero {
  background-color: var(--hero-bg-color);
  position: relative;

  &::before {
    content: '';
    display: block;
    // width: 346px;
    // height: 344px;
    // border-radius: 344px;
    width: 538px;
    height: 538px;
    border-radius: 538px;
    background-color: var(--third-logo-color);
    position: absolute;
    left: 37%;
    top: 6%;
    // top: 83px;
    // left: 36px;
    z-index: 0;
  }
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;

  padding-top: 37px;

  // padding-top: 6px;
}

.logo {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 2;

  &__text {
    font-family: Titan One;
    font-weight: 400;
    font-size: 25px;
    line-height: 1.16;
    text-align: left;
    text-transform: uppercase;
    color: var(--first-logo-color);

    margin-left: 14px;
  }

  &__icon {
    width: 50px;
    height: 50px;
    position: absolute;
    left: -67px;
  }
}

.header-btn {
  font-weight: bold;
  font-size: 16px;
  line-height: 2.75;
  display: flex;
  align-items: center;
  text-align: center;
  padding: 0 37px;
  color: var(--first-logo-color);
  background: #ffffff;
  border-radius: 22px;
  border: none;
  cursor: pointer;
  z-index: 2;

  &__icon {
    display: block;
    width: 4px;
    height: 8px;
    margin-left: 14px;
  }
}

.navigation {
  list-style: none;
  display: flex;
  align-items: center;

  &__item {
    margin-right: 35px;
    z-index: 2;
  }

  &__link {
    font-weight: 700;
    font-size: 16px;
    line-height: 1.31;
    color: var(--secondary-logo-color);
    text-decoration: none;
    z-index: 2;
  }

  &__link--current-page {
    color: var(--first-logo-color);
  }

  &__link:hover,
  &__link:focus {
    color: var(--first-logo-color);
  }

  &__item:last-child {
    margin-right: 0;
  }
}

.hero {
  display: flex;
  margin-top: 100px;
  // margin-top: 137px;
  justify-content: space-between;
  padding-bottom: 56px;

  position: relative;
}

.hero-title {
  font-family: Titan One;
  font-weight: 400;
  // font-size: 26px;
  // line-height: 1.14;
  font-size: 38px;
  line-height: 1.16;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--secondary-logo-color);

  &--alternative {
    // font-size: 48px;
    font-size: 58px;
    line-height: 0.9;
    color: var(--first-logo-color);
  }
}

.hero-list {
  display: flex;
  list-style: none;
  // margin-top: 19px;
  margin-top: 25px;

  &__link {
    // font-size: 8px;
    // line-height: 1.3;
    font-size: 14px;
    line-height: 1.28;
    font-weight: 400;
    padding: 10px 22px;
    border-radius: 22px;
    border: none;
    text-decoration: none;

    background-color: var(--first-logo-color);
    color: var(--secondary-logo-color);
    text-align: center;
    z-index: 2;
  }

  &__link:hover,
  &__link:focus {
    background-color: var(--primary-btn-hover);
  }

  &__link--alternative {
    background-color: var(--secondary-logo-color);
    color: var(--first-logo-color);
  }

  &__link--alternative:hover,
  &__link--alternative:focus {
    background-color: #ecebeb;
    color: var(--first-logo-color);
  }

  &__item:first-child {
    margin-right: 15px;
    // margin-right: 8px;
  }
}

.hero-btn {
  margin-top: 20px;
  margin-left: 180px;
  display: flex;
  justify-content: center;
  align-items: center;

  width: 40px;
  height: 40px;
  background-color: var(--secondary-logo-color);
  border-radius: 50px;
  border: none;
  cursor: pointer;

  &__icon {
    width: 12px;
    height: 12px;
    stroke: var(--first-logo-color);
  }
}

.hero-container {
  position: relative;
}

.hero-text {
  display: block;
  width: 200px;

  margin-top: 112px;
  margin-left: 180px;

  font-size: 14px;
  line-height: 1.4;
  font-weight: 400;
  color: var(--secondary-logo-color);

  &::before {
    content: '';
    display: block;
    width: 188px;
    height: 191px;
    position: absolute;
    bottom: 0;
    left: -30px;
    background-image: url(../images/hero_header_img/milk.png);
    background-repeat: no-repeat;
    background-size: contain;
  }
}

.extra-list {
  list-style: none;
  position: relative;

  &__item {
    font-family: Titan One;
    font-weight: 400;
    font-size: 46px;
    line-height: 1.15;
    text-align: right;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: var(--first-logo-color);
    z-index: 1;

    margin-bottom: 15px;

    position: relative;
  }

  &__item::after {
    content: '';
    display: block;
    width: 30px;
    height: 30px;
    border-radius: 50px;
    background-color: var(--secondary-logo-color);
    position: absolute;
    top: 0;
    right: -13px;
    z-index: -1;
  }

  &__text {
    display: block;
    font-family: DM Sans;
    font-size: 14px;
    line-height: 1.28;
    color: var(--secondary-logo-color);
    text-transform: lowercase;
  }
}

.icecream-pic {
  display: block;
  // width: 170px;
  // height: 410px;
  margin: 0;
  z-index: 0;
  width: 236px;
  height: 557px;
  background-image: url(../images/hero_header_img/icecream-pic.png);
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  bottom: 0;
  right: 30%;
}

.lady-pic {
  display: block;
  width: 293px;
  height: 232px;
  background-image: url(../images/hero_header_img/lady.png);
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  bottom: 0;
  right: 0;
}
