.contacts {
  padding-top: 45px;
  padding-bottom: 55px;
  background-image: linear-gradient(
    to bottom,
    var(--secondary-logo-color) 8.3%,
    var(--secondary-bg-color) 8.3%
  );
  @media screen and (min-width: 768px) {
    padding-top: 60px;
    padding-bottom: 80px;

    background-image: linear-gradient(
      to bottom,
      var(--secondary-logo-color) 18%,
      var(--secondary-bg-color) 18%
    );
  }
  @media screen and (min-width: 1280px) {
    padding-top: 60px;
    padding-bottom: 80px;
    background-image: url(../images/contacts_img/contacts-bg.png),
      linear-gradient(
        to bottom,
        var(--secondary-logo-color) 0 165px,
        var(--secondary-bg-color) 165px
      );
    background-position: top 150px center, center;
    background-size: contain;
    background-repeat: no-repeat;

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/contacts_img/contacts-bg-2x.png),
        linear-gradient(
          to bottom,
          var(--secondary-logo-color) 0 165px,
          var(--secondary-bg-color) 165px
        );
    }
  }
}

