.modal {
  background-color: var(--secondary-logo-color);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(360deg) scale(1);
  max-width: 600px;
  max-height: 400px;
  padding: 20px;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.2);
  width: 90vw;
  height: 90vh;
  .backdrop.is-hidden & {
    transform: translate(-50%, -50%) rotate(0deg) scale(0.5);
    transition: transform var(--animation-duration) var(--animation-timing-function);
  }

  @media screen and (min-width: 768px) {
    width: 700px;
    height: 450px;
  }

  @media screen and (min-width: 1280px) {
    max-width: 1000px;
    max-height: 700px;
  }
  &__icon {
    transition: fill var(--animation-duration) var(--animation-timing-function);
  }
}
.modal-map {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 25px 0;
  &__client-location {
    margin-bottom: 10px;
  }
}
.modal-map__client {
  position: absolute;
  top: 0;
  right: 0;
  text-align: center;
  padding: 10px;
  z-index: 100;

  box-shadow: 0px 8px 30px var(--secondary-logo-color);
  &:hover,
  &:focus {
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.2);
  }
}
.modal-map__client-location {
  height: 30%;
  position: relative;
}

.modal__close {
  display: flex;
  justify-content: center;
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  outline: 1px solid var(--secondary-bg-color);
  -ms-flex-align: center;
  align-items: center;
  background-color: var(--secondary-logo-color);
  fill: var(--first-logo-color);
  top: 8px;
  right: 8px;
  border-color: transparent;
  padding: 0px;
  &:hover,
  &:focus {
    fill: var(--secondary-logo-color);
    cursor: pointer;
    outline: 1px solid var(--secondary-logo-color);
    background-color: var(--first-logo-color);
  }
  transition: background-color var(--animation-duration) var(--animation-timing-function);
}
.modal-franchise__link {
  color: var(--first-logo-color);
  text-align: end;
}
.modal-franchise__link .text-main {
  color: var(--first-logo-color);
}
.form-registration {
  &__input {
    display: block;
    border-width: 2px;
    width: 100%;
    height: 30px;
    padding-left: 36px;
    padding-right: 12px;
    border-radius: 4px;
    border-color: var(--third-logo-color);
    outline: none;
    &:focus,
    &:not(:placeholder-shown) {
      cursor: pointer;
      border-color: var(--first-logo-color);
    }

    .form-registration__wrap-input:hover &,
    .form-registration__wrap-input:focus & {
      cursor: pointer;
      border-color: var(--first-logo-color);
    }
  }
  &__wrap-input {
    position: relative;
    overflow: hidden;
    outline: none;
  }
  &__label {
    display: block;
    text-align: left;
  }
}
.form-registration____field:not(:last-child) {
  margin-bottom: 10px;
}
.form-registration__icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  fill: var(--third-logo-color);
  transition: fill var(--animation-duration) var(--animation-timing-function);

  .form-registration__input:focus + &,
  .form-registration__input:not(:placeholder-shown) + & {
    fill: var(--first-logo-color);
  }

  .form-registration__wrap-input:hover &,
  .form-registration__wrap-input:focus & {
    fill: var(--first-logo-color);
    cursor: pointer;
  }

  &--displacement {
    left: 8px;
  }
}

.form-registration .primary-btn {
  margin-top: 10px;
  @media screen and (min-width: 768px) {
    margin-top: 15px;
  }
}

.map {
  transform: scale(1);
  transition: transform var(--animation-duration) var(--animation-timing-function);

  &:hover,
  &:focus {
    transform: scale(1.05);
    transition: transform var(--animation-duration) var(--animation-timing-function);
  }
}
